import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';


import { IonicModule } from '@ionic/angular';

import { GercriacoesPage } from './gercriacoes.page';
import { FooterModule } from '../components/footer/footer.module';
import { PaginacaoModule } from '../components/utils/paginacao/paginacao.module';

const routes: Routes = [
  {
    path: '',
    component: GercriacoesPage
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes),
    FooterModule,
    PaginacaoModule
  ],
  declarations: [GercriacoesPage]
})
export class GercriacoesPageModule { }
